html,
body {
  height: 100%;
  width: 100%;
  position: relative;
  margin: 0;
}
#root {
  height: 100%;
}
.base {
  height: 100%;
}

.cards-container {
  display: block;
}
.cards-section {
  padding-left: 8px;
}
.card {
  display: inline-table;
  padding: 8px;
  margin-bottom: 25px;
  cursor: pointer;
}
.card-name {
  text-align: center;
  margin-top: 12px;
}
.card img {
  border: 3px solid transparent;
}
.card.active {
  transform: scale(1.05);
  transition:
    transform 0.2s ease,
    box-shadow 0.2s ease;
}

.card.active img {
  box-shadow: 0px 0px 25px 6px #ffeb00;
  border: 3px solid #ffcc00;
  border-radius: 20px;
  transition:
    box-shadow 0.2s ease,
    border 0.2s ease;
}

.card.active .card-name {
  font-weight: bold;
  color: #ffcc00;
  text-shadow: 1px 1px 2px #000;
}

.header-row {
  display: flex;
  justify-content: space-between;
  position: absolute;
  left: 0;
  right: 0;
  padding: 8px;
  background-color: white;
  z-index: 1;
}
.header-row button.export {
  border-radius: 10px;
  background-color: teal;
  color: white;
  font-size: 16px;
  padding: 10px 20px;
  border: none;
  cursor: pointer;
  transition:
    background-color 0.3s ease,
    transform 0.2s ease;
}

.header-row button.export:hover {
  background-color: #008080;
  transform: scale(1.05);
}

.header-row button.export:active {
  background-color: #005f5f;
  transform: scale(0.95);
}

#pokeballPath {
  stroke: none;
  fill-rule: nonzero;
  fill: rgb(0%, 0%, 0%);
  fill-opacity: 1;
}

.flex {
  display: flex;
}
.pokemon-link {
  display: flex;
  align-items: center;
}

.series-link {
  display: flex;
  align-items: center;
}

.series-card {
  border: 1px solid black;
  padding: 8px 2px;
  margin: 0 5px;
  border-radius: 5px;
}

.display-container {
  overflow: auto;
  position: absolute;
  top: 75px;
  bottom: 0;
  left: 0;
  right: 0;
}
@media (min-width: 768px) {
  /* For larger screens */
  .display-container {
    top: 55px;
  }
}

.go-to-top-button {
  position: absolute;
  right: 25px;
  bottom: 15px;
  z-index: 1;
}
.go-to-top-button button {
  width: 50px;
  height: 50px;
  border-radius: 15px;
  cursor: pointer;
  background-color: teal;
  border: none;
  opacity: 0.7;
  transition:
    background-color 0.2s ease,
    opacity 0.2s ease,
    transform 0.1s ease;
}
.go-to-top-button button:hover {
  background-color: #008080;
  transform: scale(1.05);
  opacity: 1;
}

.selected-cards {
  background-color: #dfb300;
  border-radius: 10px;
  font-size: 16px;
  padding: 10px 20px;
  border: none;
  cursor: pointer;
  margin-left: 8px;
  color: black;
  text-decoration: none;
}
.selected-cards.active {
  box-shadow: 0px 0px 15px 5px #ffcc00;
  transition: box-shadow 0.3s ease;
}
.filterless-container {
  display: flex;
  align-items: center;
}
.filter-title {
  font-size: 18px;
  font-weight: bold;
  margin-right: 16px;
}